.about-page {
  text-align: center;
  padding: 50px;
  min-height: 100vh;
  background: white; /* Light green */

}

/* 📌 Back-End와 Front-End를 나란히 배치 */
.skills-container {
  align-items: flex-start;
  display: flex;
  gap: 80px;
  justify-content: center;
  margin: auto;
  max-width: 1200px;
  width: 100%;
  flex-wrap: wrap;
}

.backend-container, .frontend-container {
  width: 45%;
  min-width: 300px;
}

/* 📌 타이틀 스타일 */
.about-page-h3 {
  font-size: 1.8rem;
  color: #fff;
  background-color: #4caf50;
  padding: 8px 20px;
  border-radius: 20px;
  display: inline-block;
  margin-bottom: 20px;
  font-weight: bold;
  letter-spacing: 1px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

/* 📌 리스트를 2열로 배치 */
.about-page-ul {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  width: 100%;
  justify-items: center;
}

.about-page-list-hover-si{
  font-size: 13px;
}

/* 📌 카드 스타일 */
.about-page-li {
  position: relative;
  background: linear-gradient(135deg, #f0f8ff, #e6f7ff);
  border: none;
  padding: 20px;
  border-radius: 15px;
  font-size: 1.2rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 100%;
  min-width: 150px;
  height: 120px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-in-out;
  text-align: center;
}

/* 📌 마우스 오버 효과 */
.about-page-li:hover {
  background: #acefae;
  color: black;
  transform: scale(1.05);
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
}

/* 📌 아이콘 스타일 */
.AboutPage-Icons {
  margin-left: 10px;
  font-size: 35px;
  transition: transform 0.3s ease-in-out;
}

/* 📌 아이콘 흔들리는 애니메이션 */
.about-page-li:hover .AboutPage-Icons {
  transform: rotate(-10deg) scale(1.2);
}

/* 📌 반응형: 태블릿에서는 한 줄에 1개씩 */
@media (max-width: 1024px) {
  .skills-container {
    flex-direction: column;
    align-items: center;
  }

  .backend-container, .frontend-container {
    width: 100%;
  }

  .about-page-ul {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* 📌 모바일에서는 1열 배치 */
@media (max-width: 768px) {
  .about-page-ul {
    grid-template-columns: repeat(1, 1fr);
  }
}

/* 📌 Default Style */
.skill-description {
  font-size: 1.2rem;
  transition: font-size 0.3s ease-in-out, transform 0.3s ease-in-out;
  display: block; /* Ensures the transition applies properly */
}

/* 📌 Shrink text on hover */
.about-page-li:hover .skill-description {
  font-size: 0.9rem; /* Shrink slightly */
  transform: scale(0.95); /* Optional: Adds a subtle shrinking effect */
}


/* 📌 GitHub & Notion을 중앙 정렬 */
.central-container {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin-top: 30px;
}

.central-li {
  width: 300px;
  height: 100px;
  font-size: 1.4rem;
}

