.header {
    background-color: white; 
    color: black; 
    padding: 10px; 
    display: flex; 
    justify-content: space-around; 
    align-items: center; 
    height: 60px; 
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); 
    transition: background-color 0.3s ease; 
  }
  
  .header button {
    background: none; 
    border: none; 
    color: black; 
    cursor: pointer; 
    font-size: 16px; 
    padding: 8px 16px; 
    display: flex; 
    align-items: center; 
    transition: color 0.3s ease, transform 0.3s ease;
  }
  
  .header .header-icon {
    margin-right: 8px; /* Space between the icon and text */
    transition: transform 0.3s ease;
  }
  
  .header button:hover .header-icon {
    transform: rotate(20deg); /* Rotate icon on hover */
  }
  
  .header button.active {
    background-color: #6dd170; 
    color: white; 
    border-radius: 5px;
  }
  
  .header button.active:hover {
    background-color: #5cbf60;
  }
  
  .header button:hover {
    text-decoration: underline; 
    color: greenyellow; 
    transform: scale(1.1); 
  }
  