/* 프로젝트 카드 지그재그 배치 */
.project-card {
  display: flex;
  width: 90%;
  background: linear-gradient(135deg, #f0f8ff, #e6f7ff);
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 50px;
  overflow: hidden;
  position: relative;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:nth-child(even) {
  flex-direction: row-reverse;
}

/* 카드에 마우스를 올리면 약간 커지고 그림자가 커짐 */
.project-card:hover {
  transform: scale(1.02);
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.2);
}

/* 프로젝트 이미지 컨테이너 */
.project-images {
  display: flex;
  flex-direction: column;
  width: 30%;
  align-items: center;
  padding: 10px;
  grid-gap: 25px;
}

/* 이미지 애니메이션 */
.project-img, .project-img2, .WeworkHome {
  width: 100%;
  max-height: 200px;
  object-fit: contain;
  transition: transform 0.3s ease-in-out;
}

/* 마우스를 올리면 이미지 확대 효과 */
.project-img:hover, .project-img2:hover, .WeworkHome:hover {
  transform: scale(1.05);
}

/* 프로젝트 내용 */
.project-content {
  padding: 20px;
  flex: 1;
  position: relative;
}

/* 버튼 기본적으로 숨기기 */
.project-page-button {
  margin-top: 20px;
  padding: 12px 30px;
  font-size: 18px;
  background-color: #76c64f;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-right: 10px;
  opacity: 0;
  transform: translateY(10px);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

/* 카드에 마우스를 올리면 버튼이 나타남 */
.project-card:hover .project-page-button {
  opacity: 1;
  transform: translateY(0);
}

/* 버튼 호버 효과 */
.project-page-button:hover {
  background-color: #074e0d;
  transform: scale(1.1);
}
